
.header__bland {
    margin: 0;
    padding: 0;
    width: 85%;
    display: -ms-flexbox;
    display: flex;
}
.header__bland-name {
    margin: 0.5em 0 0;
    font-size: 2em;
    width: 100%;
}
.header__bland-icon {
    background-image: url('~assets/good_weather_icon.png');
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: 3%;
    width: 3em;
    height: 3em;
}



