
.top__clothes {
    height: 70%;
    position: relative;
    transform-style: preserve-3d;
    transition: .6s;
}
.top__clothes-content {
    height: 100%;
    position: relative;
    transform-style: preserve-3d;
    transition: .6s;
}
.top__clothes-back-side {
    transform: rotateY(180deg);
}
.top__clothes-front {
    height: 100%;
}



